import { Grid } from '@material-ui/core'
import Button from '@material-ui/core/Button'
import Typography from '@material-ui/core/Typography'
import withStyles from '@material-ui/core/styles/withStyles'
import PersonAddIcon from '@material-ui/icons/LocalGroceryStore'
import PropTypes from 'prop-types'
import React, { Component, Fragment } from 'react'
import { connect } from 'react-redux'
import { Link, Route } from 'react-router-dom'
import { bindActionCreators } from 'redux'

import styles from '../../../resources/theme/users'
import { fetchSuppliers } from '../SuppliersActions'
import { getSupplierListData } from '../SuppliersReducer'
import EditSupplier from './Edit'
import List from './List'
import DC from  '../pages/DistributionCenters/index'
import NewUser from './New'
import Filters from './Filters'

class Supplier extends Component {
  componentDidMount() {
    this.props.fetchSuppliers()
  }

  render() {
    const { classes, items } = this.props

    return (
      <Fragment>
        <Grid container justify="center" alignItems="center">
          <Grid item className={classes.grow}>
            <Typography variant="h3">Fornecedores</Typography>
          </Grid>
          <Grid item>
            <Button
              size="medium"
              color="primary"
              variant="contained"
              component={Link}
              to="/suppliers/new"
            >
              <PersonAddIcon className={classes.btnIcon} /> Cadastrar
            </Button>
          </Grid>
        </Grid>

        <Filters />

        <Route path="/suppliers/new" component={NewUser} />
        <Route path="/suppliers/:id/edit" component={EditSupplier} />
        <Route path="/suppliers/:id/delete" component={DeleteSupplier} />
        <Route path="/suppliers/:id/list-centers" component={DC} />

        <List classes={classes} items={items} />
      </Fragment>
    )
  }
}

Supplier.propTypes = {
  classes: PropTypes.object.isRequired,
  fetchSuppliers: PropTypes.func.isRequired,
  items: PropTypes.array.isRequired,
}

const mapStateToProps = state => ({
  ...getSupplierListData(state),
})

const mapDispatchToProps = dispatch =>
  bindActionCreators(
    {
      fetchSuppliers,
    },
    dispatch
  )
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(styles)(Supplier))
